import React from 'react';
import CardEvent from '../../components/CardEvent';

import uptapImg from '../../../../assets/images/ennova.png';

const EnnovasoftTermino = () => {
  return (
    <CardEvent
      date="26/03/2020"
      header={<h2>Ennovasoft sa de cv</h2>}
      logo={{
        src: uptapImg,
        alt: 'Ennovasoft sa de cv',
      }}
      body={<p>Culmina mi segundo contrato, y decido no renovar para seguir nuevas metas.</p>}
      footer={
        <div className="links">
          <a href="http://ennovasoft.com/" target="_blank" rel="noopener noreferrer">
            Página web
          </a>
        </div>
      }
    />
  );
};

export default EnnovasoftTermino;
