import React from 'react';
import CardEvent from '../../components/CardEvent';

import {
  phpImg,
  javascriptImg,
  cssImg,
  htmlImg,
  bootstrapImg,
  vueImg,
  mysqlImg,
} from '../../../Icons';

import ufmImg from '../../../../assets/images/ufm.png';

const PagosUniversidad = () => {
  return (
    <CardEvent
      date="01/02/2019 - 25/03/2019"
      logo={{
        src: ufmImg,
        alt: 'universidad filadelfia de méxico',
        className: 'bg-white',
      }}
      header={<h2>Control de pagos</h2>}
      body={
        <>
          <p>
            Desarrollo de sistema que gestiona pagos de los alumnos para la universidad
            Filadelfia de méxico.
          </p>
          <div className="links">
            <a
              href="https://universidadfiladelfia.edu.mx/"
              target="_blank"
              rel="noopener noreferrer">
              Página web
            </a>
          </div>
        </>
      }
      footer={
        <div className="icons">
          <img src={phpImg} alt="" />
          <img src={mysqlImg} alt="" />
          <img src={javascriptImg} alt="" />
          <img src={vueImg} alt="" />
          <img src={htmlImg} alt="" />
          <img src={cssImg} alt="" />
          <img src={bootstrapImg} alt="" />
        </div>
      }
    />
  );
};

export default PagosUniversidad;
