import React from 'react';
import CardEvent from '../../components/CardEvent';
import uptapImg from '../../../../assets/images/ennova.png';
import {
  javaImg,
  javascriptImg,
  cssImg,
  bootstrapImg,
  htmlImg,
  mysqlImg,
  springImg,
} from '../../../Icons';

const EnnovasoftInicio = () => {
  return (
    <CardEvent
      date="16/02/2018"
      header={<h2>Ennovasoft sa de cv</h2>}
      logo={{
        src: uptapImg,
        alt: 'Ennovasoft sa de cv',
      }}
      body={
        <p>
          Inicio en el puesto de Desarrollador Full stack, enfocado en desarrollo web, en mis
          roles esta la creación de paginas estáticas y aplicaciones web
        </p>
      }
      footer={
        <>
          <div className="links">
            <a href="http://ennovasoft.com/" target="_blank" rel="noopener noreferrer">
              Página web
            </a>
          </div>
          <div className="icons">
            <img src={javascriptImg} alt="" />
            <img src={javaImg} alt="" />
            <img src={mysqlImg} alt="" />
            <img src={cssImg} alt="" />
            <img src={htmlImg} alt="" />
            <img src={springImg} alt="" />
            <img src={bootstrapImg} alt="" />
          </div>
        </>
      }
    />
  );
};

export default EnnovasoftInicio;
