import React from 'react';
import CardEvent from '../../components/CardEvent';

import {
  phpImg,
  javascriptImg,
  cssImg,
  htmlImg,
  bootstrapImg,
  prestashopImg,
} from '../../../Icons';

import oxiumImg from '../../../../assets/images/oxium.png';

const Freelance = () => {
  return (
    <CardEvent
      date="05/05/2020 - 28/12/2020"
      logo={{
        src: oxiumImg,
        alt: 'oxium mx',
        className: 'bg-pink-500',
      }}
      header={<h2>Freelance - Oxium</h2>}
      body={
        <p>Desarrollador freelance, desarrollo de paginas web estáticas, y tienda online</p>
      }
      footer={
        <>
          <div className="links">
            <a href="https://oxium.mx/" target="_blank" rel="noopener noreferrer">
              Página web
            </a>
          </div>
          <div className="icons">
            <img src={phpImg} alt="" />
            <img src={javascriptImg} alt="" />
            <img src={htmlImg} alt="" />
            <img src={cssImg} alt="" />
            <img src={bootstrapImg} alt="" />
            <img src={prestashopImg} alt="" />
          </div>
        </>
      }
    />
  );
};

export default Freelance;
