import React from 'react';
import CardEvent from '../components/CardEvent';

import uptapImg from '../../../assets/images/uptap.png';
import { androidImg, arduinoImg, javaImg, htmlImg, bootstrapImg } from '../../Icons';

const Universidad = () => {
  return (
    <CardEvent
      date="2012 - 2016"
      logo={{
        src: uptapImg,
        alt: 'Universidad Politécnica de Tapachula',
        className: 'bg-white',
      }}
      header={<h2>Universidad Politécnica de Tapachula</h2>}
      body={
        <p>Estudios especializados en el diseño, desarrollo e implementación de software</p>
      }
      footer={
        <>
          <div className="links">
            <a
              href="http://uptapachula.edu.mx/page/"
              target="_blank"
              rel="noreferrer"
              alt="#uptap">
              Página web
            </a>
          </div>

          <div className="icons">
            <img src={javaImg} alt="" />
            <img src={htmlImg} alt="" />
            <img src={bootstrapImg} alt="" />
            <img src={arduinoImg} alt="" />
            <img src={androidImg} alt="" />
          </div>
        </>
      }
    />
  );
};

export default Universidad;
