import React from 'react';
import CardEvent from '../../components/CardEvent';

import {
  phpImg,
  javascriptImg,
  cssImg,
  htmlImg,
  bootstrapImg,
  vueImg,
  mysqlImg,
} from '../../../Icons';

const Laboratorio = () => {
  return (
    <CardEvent
      date="15/06/2018 - 10/09/2018"
      header={<h2>Laboratorio de análisis clínicos - V1</h2>}
      body={
        <p>
          Desarrollo de sistema para el manejo de usuarios, pacientes, y resultados de análisis
          clínicos para laboratorio privado
        </p>
      }
      footer={
        <div className="icons">
          <img src={phpImg} alt="" />
          <img src={mysqlImg} alt="" />
          <img src={javascriptImg} alt="" />
          <img src={vueImg} alt="" />
          <img src={htmlImg} alt="" />
          <img src={cssImg} alt="" />
          <img src={bootstrapImg} alt="" />
        </div>
      }
    />
  );
};

export default Laboratorio;
