import React from 'react';
import CardEvent from '../../components/CardEvent';

import {
  phpImg,
  javascriptImg,
  cssImg,
  htmlImg,
  bootstrapImg,
  vueImg,
  mysqlImg,
} from '../../../Icons';

const Laboratorio = () => {
  return (
    <CardEvent
      date="01/02/2019 - 25/03/2019"
      header={<h2>Laboratorio de análisis clínicos - V2</h2>}
      body={<p>Actualización y creación de módulos</p>}
      footer={
        <div className="icons">
          <img src={phpImg} alt="" />
          <img src={mysqlImg} alt="" />
          <img src={javascriptImg} alt="" />
          <img src={vueImg} alt="" />
          <img src={htmlImg} alt="" />
          <img src={cssImg} alt="" />
          <img src={bootstrapImg} alt="" />
        </div>
      }
    />
  );
};

export default Laboratorio;
